import React from 'react';

export function useClickOutsideRef(
  callback: any,
  ref: React.RefObject<HTMLElement>,
) {
  React.useEffect(() => {
    const handler = (event: MouseEvent) => {
      const { target } = event;
      if (
        target instanceof Node &&
        ref.current &&
        ref.current !== target &&
        !ref.current.contains(target)
      ) {
        callback();
      }
    };
    window.document.addEventListener('mousedown', handler, { capture: true });
    return () => {
      window.document.removeEventListener('mousedown', handler, {
        capture: true,
      });
    };
  }, [callback, ref]);
}

export function useClickOutsideAttr(callback: any, attr: string) {
  React.useEffect(() => {
    const selector = `[${attr}]`;
    const handler = (event: MouseEvent) => {
      let { target } = event;
      if (target instanceof Node && target instanceof Element === false) {
        target = target.parentElement;
      }
      if (
        target instanceof Element &&
        !target.closest(selector) &&
        !target.hasAttribute(attr)
      ) {
        callback();
      }
    };
    window.document.addEventListener('mousedown', handler, { capture: true });
    return () => {
      window.document.removeEventListener('mousedown', handler, {
        capture: true,
      });
    };
  }, [attr, callback]);
}
export function useClickInsideAttr(
  callback: any,
  attr: string,
  excludeAttr: string,
) {
  React.useEffect(() => {
    const selector = `[${attr}]`;
    const excludeSelector = `[${excludeAttr}]`;
    const handler = (event: MouseEvent) => {
      let { target } = event;
      if (target instanceof Node && target instanceof Element === false) {
        target = target.parentElement;
      }
      if (target instanceof Element && !target.closest(excludeSelector)) {
        if (target.closest(selector) || target.hasAttribute(attr)) callback();
      }
    };
    window.document.addEventListener('mousedown', handler, { capture: true });
    return () => {
      window.document.removeEventListener('mousedown', handler, {
        capture: true,
      });
    };
  }, [attr, callback, excludeAttr]);
}
